import React, {useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminClienteDetail.css';
import * as FIcon from "react-feather";
import {AdminItemDetail, Page} from "../../components";
import {DetailViewProps, useApiToaster, useConfirm} from "../../helpers-ts";
import {RUTAS} from "../../constants";

const defaultValues = {
  id: '',
  first_name: '',
  last_name: '',
  nro_documento: '',
  razon_social: '',
  email: '',
  ruc: '',
  nro_telefono: '',
  imagen_perfil: null,
  direcciones: [],
}

export const AdminClienteDetail: React.FC<DetailViewProps> = observer(({history, match}) => {
  const rootStore = useStores()
  const {adminStore} = rootStore
  const [Loading, setLoading] = useState(false)
  const [Cliente, setCliente] = useState<any>(adminStore.clienteDetalle || defaultValues)
  const apiToaster = useApiToaster()
  const idCliente = match.params.id

  const setup = () => {
    obtenerCliente(idCliente)
  }

  useEffect(() => {
    setup()
  }, [idCliente])

  const obtenerCliente = (cliente_id: number) => {
    // @ts-ignore
    rootStore.environment.api.getCliente(cliente_id).then(result => {
      if (result.kind === "ok") {
        adminStore.setClienteDetalle(result.cliente)
        setCliente(result.cliente)
      }
    })
  }

  const editar = (row: any) => {
    adminStore.setClienteDetalle(row)
    history.push(RUTAS.ADMIN_CLIENTE_UPDATE(row.id))
  }

  const agregarDireccion = () => {
    adminStore.setDireccionDetalle(null)
    history.push(RUTAS.ADMIN_CREATE_DIRECCION_CLIENTE(Cliente.id))
  }

  const editarDireccion = (row: any) => {
    adminStore.setDireccionDetalle(row)
    history.push(RUTAS.ADMIN_UPDATE_DIRECCION_CLIENTE(Cliente.id, row.id))
  }

  const [showConfirmEliminar, ConfirmEliminarDialog] = useConfirm((row: any) => {
    rootStore.environment.api.eliminarCliente(row.id).then((result: any) => {
      setLoading(false)
      apiToaster(result, "Eliminado satisfactoriamente", "eliminar")
      if (result.kind === "ok") {
        history.push('/admin/cliente')
      }
    })
  })

  const eliminar = (row: any) => {
    showConfirmEliminar('Confirma que desea eliminar el cliente?', row)
  }

  const [showConfirmEliminarDireccion, ConfirmEliminarDireccionDialog] = useConfirm((row: any) => {
    rootStore.environment.api.eliminarDireccion(row.id).then((result: any) => {
      setLoading(false)
      if (!adminStore.clienteDetalle) {
        return
      }
      apiToaster(result, "Eliminado satisfactoriamente", "eliminar")
      if (result.kind === "ok") {
        const puntosRetiro = [...adminStore.clienteDetalle.direcciones]
        const puntoRet = puntosRetiro.find(pret => pret.id === row.id)
        if (puntoRet) {
          puntosRetiro.splice(puntosRetiro.indexOf(puntoRet), 1)
          adminStore.clienteDetalle.setDirecciones(puntosRetiro)
        }
      }
      // @ts-ignore
      obtenerCliente(adminStore.clienteDetalle.id)
    })
  })

  const eliminarDireccion = (row: any) => {
    showConfirmEliminarDireccion('Confirma que desea eliminar?', row)
  }

  return (
    <Page login_required admin>
      {ConfirmEliminarDireccionDialog}
      {ConfirmEliminarDialog}
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">{Cliente.first_name}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push(RUTAS.ADMIN)}>Inicio</a></li>
                <li className="breadcrumb-item"><a href={RUTAS.ADMIN_CLIENTE}>Clientes</a></li>
                <li className="breadcrumb-item active">{Cliente.first_name}</li>
              </ol>
            </div>
          </div>
          <div className={'mb-2 text-right'}>
            <button className={'btn btn-primary ml-3'} title={'Editar'} onClick={() => editar(adminStore.clienteDetalle)}>
              <FIcon.Edit size={14}/> Editar
            </button>
            <button className={'btn btn-primary ml-3'} title={'Eliminar'}
                    onClick={() => eliminar(adminStore.clienteDetalle)}><FIcon.Trash size={14}/> Eliminar
            </button>
          </div>
          <div className="card">
            <div className="card-body">
              <div className={'row'}>
                <AdminItemDetail titulo={'Nombre'} texto={Cliente.first_name} columnas={3}/>
                <AdminItemDetail titulo={'Apellido'} texto={Cliente.last_name} columnas={3}/>
                <AdminItemDetail titulo={'Email'} texto={Cliente.email} columnas={3}/>
                <AdminItemDetail titulo={'Email'} texto={Cliente.email} columnas={3}/>
                <AdminItemDetail titulo={'Número de documento'} texto={Cliente.nro_documento} columnas={3}/>
                <AdminItemDetail titulo={'Número de celular'} texto={Cliente.nro_telefono} columnas={3}/>
                <AdminItemDetail titulo={'Razón social'} texto={Cliente.razon_social} columnas={3}/>
                <AdminItemDetail titulo={'RUC'} texto={Cliente.ruc} columnas={3}/>
              </div>
            </div>
          </div>
          <h4>Direcciones</h4>
          <div className="card">
            <div className="card-body">
              <div className={'mb-2 text-right'}>
                <button className={'btn btn-primary'} title={'Agregar dirección'} onClick={() => agregarDireccion()}>
                  <FIcon.Plus size={14}/> Agregar dirección
                </button>
              </div>
              <div className={'table-responsive'}>
                <table className={'table'}>
                  <thead>
                  <tr>
                    <td>Nombre</td>
                    <td>Calle Principal</td>
                    <td>Calle Secundaria</td>
                    <td>Número</td>
                    <td>Referencia</td>
                    <td>Ciudad</td>
                    <td>Predeterminada</td>
                    <td>Acciones</td>
                  </tr>
                  </thead>
                  <tbody>
                  {adminStore.clienteDetalle?.direcciones.map((dir:any) => <tr key={`dir-key-${dir.nombre}`}>
                    <td>{dir.nombre}</td>
                    <td>{dir.calle_principal}</td>
                    <td>{dir.calle_secundaria}</td>
                    <td>{dir.numero}</td>
                    <td>{dir.referencia}</td>
                    <td>{dir.ciudadO.nombre}</td>
                    <td>{dir.default ? 'Si' : 'No'}</td>
                    <td>
                    <span className={'button'} title={'Editar'} onClick={() => editarDireccion(dir)}><FIcon.Edit
                      size={14}/></span>
                      <span className={'button ml-3'} title={'Eliminar'}
                            onClick={() => eliminarDireccion(dir)}><FIcon.Trash size={14}/></span>
                    </td>
                  </tr>)}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminClienteDetail;
